import React, { Component } from "react";
import Jumbotron from "../components/Jumbotron";

class Home extends Component {
    render() {
        return (
            <div>
                <Jumbotron>
                    <h3>Puzzle corp.</h3>
                    </Jumbotron>
            
            </div>
        )
    }
}
export default Home;


